body.active-popup {
  overflow-y: hidden;
}

.btn-popup {
  padding: 10px 5px;
  color: white;
  display: block;
  margin: 85px auto 0;
  font-size: 18px;
  cursor: pointer;
  border-radius: 50%;
  box-shadow: 0px 2px 2px lightgray;
  background-color: #ffc107;
  position: absolute;
  right: 66%;
  top: 0px;
  transition: ease background-color 250ms;
}

.btn-popup:hover {
  background-color: #4dca9c;
}

.popup,
.overlay {
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: fixed;
}

.overlay {
  background: rgba(49, 49, 49, 0.8);
}
.popup-content {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  line-height: 1.4;
  background: #f1f1f1;
  padding: 14px 28px;
  border-radius: 3px;
  max-width: 600px;
  min-width: 300px;
}

.close-popup {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 5px 7px;
}

@media screen 
  and (min-device-width: 768px) 
  and (max-device-width: 1600px) 
  { 
    .popup, .overlay {
      height: 120vh;
    }
}

@media screen and (max-width: 900px) 
  { 
    .popup, .overlay {
      height: 120vh;
    }

    .popup-content {
      min-width: 350px;

     
    }

    
  }
